.articles-main {
  @include bg-image("hero-mobile@1x.jpg");
  background-position: top center;
  background-size: auto 368px;
  background-color: $index-page-color;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: center;
    background-size: auto;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: auto 739px;
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }
  }
}

.articles-main__title {
  padding-bottom: 120px;

  @media (min-width: $tablet-width) {
    padding-top: 110px;
    padding-bottom: 372px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 113px;
    padding-bottom: 356px;
  }
}

.articles-main__list {
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet-width - 1) {
    width: auto;
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.articles-main__item {
  position: relative;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    width: 50%;
  }

  &--decor {
    background-color: $bgcolor-articles-lighter;

    &:hover {
      background-color: $bgcolor-articles-lighter-hover;

      &::before {
        background-color: $bgcolor-articles-lighter-hover;
      }
    }
  }

  &--toys {
    background-color: $bgcolor-toys-darker;

    &:hover {
      background-color: $bgcolor-toys-darker-hover;

      &::before {
        background-color: $bgcolor-toys-darker-hover;
      }
    }
  }
}

.articles-main__link {
  display: block;
  padding-top: 24px;
  padding-bottom: 31px;
  padding-left: 30px;
  font-weight: bold;
  font-size: 19px;
  color: $bgcolor-white;
  background-color: inherit;
  transition: transform 0.3s;

  &::after {
    position: absolute;
    opacity: 0.2;
  }

  &:hover,
  &:focus {
    transform: translateY(-5px);

    &::after {
      opacity: 0.3;
    }

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: -4px;
      left: 0;
      height: 5px;
    }
  }

  &:active {
    &::after {
      opacity: 0.1;
    }
  }

  &--decor {
    &::after {
      content: url("../img/icon-interior.svg");
      top: 24px;
      right: 36px;

      @media (min-width: $tablet-width) {
        top: 30px;
      }
    }
  }

  &--toys {
    &::after {
      content: url("../img/icon-toy.svg");
      top: 22px;
      right: 28px;

      @media (min-width: $tablet-width) {
        top: 31px;
        right: 34px;
      }
    }
  }

  span {
    display: inline-block;
    width: 130px;
  }

  @media (min-width: $tablet-width) {
    padding-bottom: 34px;
    padding-left: 36px;
    font-size: 20px;
    line-height: 30px;
  }
}
